@function max($numbers...) {
	@return m#{a}x(#{$numbers});
}

@function min($numbers...) {
	@return m#{i}n(#{$numbers});
}

*{
 box-sizing: border-box;
 font-family: 'Manrope', sans-serif;
 font-weight: normal;
}

h1,h2,h3,h4,h5,h6,p{
	margin: 0;
}

body{
	position: relative;
	margin: 0;
}

main{
	overflow: hidden;
}

input{
	border: none;
	outline: none;
	&:focus,
	&:focus-within,
	&:active,
	&:visited{
		outline: none;
		border: none;
	}
}
button{
	border: none;
	outline: none;
	&:focus,
	&:focus-within,
	&:active,
	&:visited{
		border: none;
		outline: none;
		&-visible{
			border: none;
			outline: none;
		}
	}
}

.logo{
	width: 230px;
	height: 49px;
	display: flex;
	align-items: center;
	img{
		width: 100%;
		object-fit: scale-down;
	}
	&--footer{
		margin-left: -87px;
	}
}
@media (max-width: 992px){
	.logo{
		&--footer{
			margin-left: 0;
			order: 1;
		}
	}
}

.detector {
	font-size: 1em;
	letter-spacing: 0.05em;
	position: relative;
	.detector__led{
		position: absolute;
		left: -24px;
		transform: translateY(-50%);
		top: 50%;
		width: 13px;
		height: 13px;
		border-radius: 50%;
		background-color: red;
		&--active{
			background-color: #44BD6D;
		}
	}
	&__text{
		text-transform: uppercase;
	}
}

a{
	&:hover{
		text-decoration: none;
	}
}

.phone{
	font-size: 21px;
	font-weight: 600;
	line-height: 29px;
	display: flex;
	align-items: center;
	a{
		color: #282E35;
	}
}

.hamburger{
	width: 25px;
	max-height: 21px;
	align-self: center;
	span{
		display: block;
		height: 3px;
		margin-bottom: 6px;
		background-color: #000;
		transition: .3s transform;
	}
	
	&--active{
		span{
			margin-bottom: 0;
			&:first-child{
				transform: rotate(45deg) translateY(3px);
			}
			&:nth-child(2){
				display: none;
			}
			&:last-child{
				transform: rotate(-45deg) translateY(-2px);
			}
		}
	}
}

.line{
	width: 100%;
	height: 1px;
	background-color: #f6f6f6;
	margin: 0 5px;
}

.menu {
	&__item{
		font-size: 15px;
		text-transform: uppercase;
		margin-bottom: 40px;
	}
}
.text{
	font-size: 18px;
}
.shrub{
	bottom: -78px;
}
.bird{
    top: -108px;
    right: -95px;
}
.orange{
	color: #F69A1B;
	font-weight: 800;
}
.leaf{
	position: absolute;
	left: -25px;
	top: 28px;
}
 .title {
 	font-weight: 800;
 	font-size: 50px;
 	line-height: 68px;
 	text-align: center;
 	color: #282E35;
 	margin-bottom: 46px;
 }
@media (max-width: 1450px){
