		height: 114.64px;
	}
}

.descr {
	font-size: 17px;
	line-height: 30px;
	color: #4E5865;
}

.slick-arrow {
	position: absolute;
	font-size: 0;
	width: 16px;
	height: 32px;

	&.slick-prev {
		background: url('../icons/slider-arrow-prev.svg') center center/cover no-repeat;
	}

	&.slick-next {
		background: url('../icons/slider-arrow-next.svg') center center/cover no-repeat;
	}

	&:focus {
		outline: none;
	}
}

.slick-dots {
	display: flex;
	padding-left: 0;
	justify-content: center;
	bottom: 0;

	button {
		display: none;
	}

	li {
		width: 14px;
		height: 14px;
		margin: 0 8px;
		border: 2px solid #333E4A;
		border-radius: 100%;

		&::marker {
			font-size: 0;
		}
	}

	.slick-active {
		background-color: #333E4A;
	}
}

@media (max-width: 576px) {
	.descr {
		font-size: 14px;
		line-height: 24px;
		margin-top: 20px;
	}
}
