 .form {
 	&-popup {
		padding: 55px 49px 66px;
 		max-width: 443px;
 	}

 	&__title {
 		font-size: 36px;
 		margin-bottom: 15px;
 	}

 	&__subtitle {
 		font-size: 17px;
 		margin-bottom: 45px;
 	}

 	&__inputs {
		&-row{
			display: flex;
			justify-content: space-between;	
		}
 		&-col {
 			display: flex;
 			flex-direction: column;
 		}
 	}

 	&__input {
 		border-radius: 2px;

 		&-text {
 			border: 1px solid #EAEAEA;
 			box-shadow: inset 1px -3px 4px rgba(176, 176, 176, 0.25);
 			border-radius: 2px;
 			padding: 25px;
 			font-size: 18px;
 			letter-spacing: 0.3em;
 			color: #3A4756;
 			max-height: 70px;
 		}

 		&-btn {
 			padding: 27px 15px;
 			margin-top: 35px;
 			line-height: calc(100% - 16px);
			height: 72px;
 		}
		 &-wrapper{
			margin-bottom: 27px;
			display: flex;
			align-items: center;
		 }
		&-radio{
			display: none;
			& + &--circle{
				position: relative;
				width: 23px;
				height: 23px;
				margin-right: 13px;
				border: 1px solid #CACACA;
				border-radius: 100%;
				&:before{
					content: "";
					position: absolute;
					width: 11px;
					height: 11px;
					left: 0;
					right: 0;
					top: 0;
					bottom: 0;
					margin: auto;
					background-color: transparent;
					border-radius: 100%;
				}
			}
			&:checked + &--circle{
				&:before{
					background-color: #F69A1B;
				}
			}
		}
 	}
 }


